/* eslint-disable func-names */
// —Ñ–∏–ª—å—Ç—Ä–∞—Ü–∏—è –≤–≤–æ–¥–∏–º–æ–≥–æ —Å–æ–æ–±—â–µ–Ω–∏–µ –Ω–∞ –Ω–∞–ª–∏—á–∏–µ —Å—Å—ã–ª–æ–∫

export default function (str) {
  let re = /([^\"=]{2}|^)((https?|ftp):\/\/\S+[^\s.,> )\];'\"!?])/;
  let subst = '$1<a href="$2" target="_blank">$2</a>';
  return str.replace(re, subst);
}
